.tooltip {
  visibility: hidden;
  position: absolute;
  top: -33px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #333;
  font-size: 13px;
  color: #fff;
  text-align: center;
  opacity: 0;
  transition: opacity 0.4s ease;
}
.tooltip .inner {
  position: relative;
  padding: 1px 8px 2px 8px;
}
.tooltip .inner:after {
  top: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(136, 183, 213, 0);
  border-top-color: #333;
  border-width: 8px;
  margin-left: -8px;
}

#snackbar {
  visibility: hidden;
  min-width: 250px;
  background-color: #333;
  color: #fff;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 9999999;
  left: 50%;
